body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
}

h3 {
  font-weight: 500;
}

.card {
  box-sizing: border-box;
  background-color: #f3f3f3;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  height: 408px;
  display: flex;
}

.card__image {
  object-fit: contain;
  object-position: center;
  width: 160px;
  height: 134px;
  margin: 0 auto;
  padding: 32px 19px 0;
}

.card__product-name {
  text-align: start;
  line-height: 18px;
  font-weight: 500px;
  color: #060b35;
  height: 36px;
  margin: 0;
  padding: 40px 16px 4px;
  font-size: 12px;
}

.card__product-code {
  color: #616070;
  justify-content: start;
  height: 14px;
  margin: 0 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__review {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin: 16px 16px 0;
  display: flex;
}

.card__review-count {
  text-align: center;
  margin-top: 2px;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 18px;
  margin: 24px 16px 0;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-tag {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #f3f3f3;
  text-transform: uppercase;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #f3f3f3;
  border: 1px solid #00acdc;
}

.stars {
  flex-direction: row;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

